import { createStore } from "vuex";

export default createStore({
  state: {
    clogcss: `
        color: white;
        border-radius: 10px;
        text-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px, rgba(240, 46, 170, 0.2) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px, rgba(240, 46, 170, 0.05) 25px 25px;
    `,
    hideConfigButton: false,
    isPinned: true,
    showConfig: false,
    sidebarType: "bg-white",
    isRTL: false,
    mcolor: "",
    darkMode: false,
    isNavFixed: false,
    isAbsolute: false,
    showNavs: true,
    showSidenav: true,
    showNavbar: true,
    showFooter: true,
    showMain: true,
    layout: "default",
    data: [],
    cacheddata: []
  },
  mutations: {
    toggleConfigurator(state) {
      state.showConfig = !state.showConfig;
    },
    navbarMinimize(state) {
      const sidenav_show = document.querySelector(".g-sidenav-show");

      if (sidenav_show.classList.contains("g-sidenav-hidden")) {
        sidenav_show.classList.remove("g-sidenav-hidden");
        sidenav_show.classList.add("g-sidenav-pinned");
        state.isPinned = true;
      } else {
        sidenav_show.classList.add("g-sidenav-hidden");
        sidenav_show.classList.remove("g-sidenav-pinned");
        state.isPinned = false;
      }
    },
    sidebarType(state, payload) {
      state.sidebarType = payload;
    },
    navbarFixed(state) {
      if (state.isNavFixed === false) {
        state.isNavFixed = true;
      } else {
        state.isNavFixed = false;
      }
    },
    async getData(state){
      console.log("%cVeriler sırasıyla alınmaya başlandı.", state.clogcss + 'font-size: 20px;');
      await fetch('stats.json')
      .then(res => res.json())
      .then(data => state.data.stats = data.stats)
      .catch(err => console.log("stats.json adlı dosyaya ulaşırken bir sıkıntı oluştu." + err.message))
      .then(console.log("%cstats.json yüklendi.", state.clogcss + 'font-size: 16px;'))

      await fetch('chart.json')
      .then(res => res.json())
      .then(data => state.data.chart = data.chart)
      .catch(err => console.log("chart.json adlı dosyaya ulaşırken bir sıkıntı oluştu. Sayfayı yenilemeyi deneyiniz." + err.message))
      .then(console.log("%cchart.json yüklendi.", state.clogcss + 'font-size: 16px;'))

      await fetch('carousel.json')
      .then(res => res.json())
      .then(data => state.data.carousel = data.carousel)
      .catch(err => console.log("carousel.json adlı dosyaya ulaşırken bir sıkıntı oluştu. Sayfayı yenilemeyi deneyiniz." + err.message))
      .then(console.log("%ccarousel.json yüklendi.", state.clogcss + 'font-size: 16px;'))

      await fetch('sales.json')
      .then(res => res.json())
      .then(data => state.data.sales = data.sales)
      .catch(err => console.log("sales.json adlı dosyaya ulaşırken bir sıkıntı oluştu. Sayfayı yenilemeyi deneyiniz." + err.message))
      .then(console.log("%csales.json yüklendi.", state.clogcss + 'font-size: 16px;'))

      await fetch('categories.json')
      .then(res => res.json())
      .then(data => {
        state.data.categories = data.categories
        state.data.categories.cardtitle = data.cardtitle
      })
      .catch(err => console.log("categories.json adlı dosyaya ulaşırken bir sıkıntı oluştu. Sayfayı yenilemeyi deneyiniz." + err.message))
      .then(console.log("%ccategories.json yüklendi.", state.clogcss + 'font-size: 16px;'))

      await fetch('authorstable.json')
      .then(res => res.json())
      .then(data => state.data.authorstable = data.table)
      .catch(err => console.log("authorstable.json adlı dosyaya ulaşırken bir sıkıntı oluştu. Sayfayı yenilemeyi deneyiniz." + err.message))
      .then(console.log("%cauthorstable.json yüklendi.", state.clogcss + 'font-size: 16px;'))

      await fetch('projectstable.json')
      .then(res => res.json())
      .then(data => state.data.projectstable = data.projectstable)
      .catch(err => console.log("authorstable.json adlı dosyaya ulaşırken bir sıkıntı oluştu. Sayfayı yenilemeyi deneyiniz." + err.message))
      .then(console.log("%cprojectstable.json yüklendi.", state.clogcss + 'font-size: 16px;'))

      await fetch('cardinfos.json')
      .then(res => res.json())
      .then(data => {
        state.data.cards = data.cards
        state.cacheddata.cards = {master: {}}
        state.cacheddata.cards.master.billinginfo = data.cards.master.billinginfo
      })
      .catch(err => console.log("cardinfos.json adlı dosyaya ulaşırken bir sıkıntı oluştu. Sayfayı yenilemeyi deneyiniz." + err.message))
      .then(console.log("%ccardinfos.json yüklendi.", state.clogcss + 'font-size: 16px;'))

      await fetch('profile.json')
      .then(res => res.json())
      .then(data => {
        state.data.profile = data.profile
        state.cacheddata.profile = data.profile
      })
      .catch(err => console.log("profile.json adlı dosyaya ulaşırken bir sıkıntı oluştu. Sayfayı yenilemeyi deneyiniz." + err.message))
      .then(console.log("%cprofile.json yüklendi.", state.clogcss + 'font-size: 16px;'))

      await fetch('notifications.json')
      .then(res => res.json())
      .then(data => state.data.notifications = data.notifications)
      .catch(err => console.log("notifications.json adlı dosyaya ulaşırken bir sıkıntı oluştu. Sayfayı yenilemeyi deneyiniz." + err.message))
      .then(console.log("%cnotifications.json yüklendi.", state.clogcss + 'font-size: 16px;'))

      await fetch('virtualreality.json')
      .then(res => res.json())
      .then(data => state.data.vreality = data.vreality)
      .catch(err => console.log("virtualreality.json adlı dosyaya ulaşırken bir sıkıntı oluştu. Sayfayı yenilemeyi deneyiniz." + err.message))
      .then(console.log("%cvirtualreality.json yüklendi.", state.clogcss + 'font-size: 16px;'))
    },
    updateMessage(state, {message, token, isoptimize}) {
      if(isoptimize){
        switch(token)
        {
          case 'username':
            state.cacheddata.profile.userinfo.name = message
          break;
          case 'email':
            state.cacheddata.profile.userinfo.email = message
          break;
          case 'firstname':
            state.cacheddata.profile.userinfo.firstname = message
          break;
          case 'lastname':
            state.cacheddata.profile.userinfo.lastname = message
          break;
        }
      }
      else{
        switch(token)
        {
          case 'address':
            state.cacheddata.profile.contactinfo.address = message
          break;
          case 'city':
            state.cacheddata.profile.contactinfo.city = message
          break;
          case 'country':
            state.cacheddata.profile.contactinfo.country = message
          break;
          case 'postalcode':
            state.cacheddata.profile.contactinfo.postalcode = message
          break;
          case 'about':
            state.cacheddata.profile.contactinfo.about = message
          break;
        }
      }
    },
    updateBillingInfo(state, {obj, token, objdata, newkey}){
      console.log("%cFaturalandırma bilgileri başarıyla güncellendi", state.clogcss + 'color:white; font-size: 22px;')
      if(token == 'delete')
      {
        delete state.cacheddata.cards.master.billinginfo[obj]
        if(state.cacheddata.cards.master.billinginfo == {})
        {
           state.cacheddata.cards.master.billinginfo.isnull = true
        }
      }
      else if(token === 'add')
      {
        const objdataCopy = Object.assign({}, objdata)
        state.cacheddata.cards.master.billinginfo[obj] = objdataCopy
      }
      else if(token === 'edit')
      {
        const objdataCopy = Object.assign({}, objdata)
        state.cacheddata.cards.master.billinginfo[newkey] = objdataCopy
        delete state.cacheddata.cards.master.billinginfo[obj]
      }
    }
    
  },
  actions: {
    toggleSidebarColor({ commit }, payload) {
      commit("sidebarType", payload);
    }
  },
  getters: {
    getStatsData(state){
      return state.data.stats
    },
    getChartData(state){
      return state.data.chart
    },
    getCarouselData(state){
      return state.data.carousel
    },
    getSalesData(state){
      return state.data.sales
    },
    getCategoriesData(state){
      return state.data.categories
    },
    getCategoriesTitle(state){
      return state.data.categories.cardtitle
    },
    getAuthorsTable(state){
      return state.data.authorstable
    },
    getProjectsTable(state){
      return state.data.projectstable
    },
    getCardDatas(state){
      return state.data.cards
    },
    // TEST AMACLIDIR
    getCardBillingData(state){
      return (state.data.cards) ? state.cacheddata.cards.master.billinginfo : null
    },
    getProfileData(state){
      return state.cacheddata.profile
    },
    getNotificationData(state){
      return state.data.notifications
    },
    getVirtualRealityData(state){
      return state.data.vreality
    }
  }
});
