                    <argon-input type="text" placeholder="Example: Alexa Jordan" aria-label="Name" :size="(this.$store.state.darkMode ? 'text-white' : 'text-dark') + ' bg-transparent'" isRequired @input="e => newbill.name = e.target.value"/>
