<script>

import Askresponse2 from "@/examples/modals/Modal3.vue";
import ArgonInput from "@/examples/Forms/Input.vue";
import ArgonButton from "@/components/ArgonButton.vue";


export default {
  name: "billing-card",
  data(){
    return {
      modals: {
        addbill: false
      },
      newbill: {
          name: "",
          company: "",
          email: "",
          vatno: ""
      }
    }
  },
  components: {
    Askresponse2,
    ArgonInput,
    ArgonButton
  },
  props: {
    billingdata: {
      default: null
    },
  },
  methods:{
    addBillingInfo(index, token, data){
      index = index.toLowerCase().replace(/\s/g, '')
      console.log("Hedef: ../cardinfos.json Eklenen kişi: " + index);
      this.$store.commit('updateBillingInfo', {obj: index, token: token, objdata: data})
    },
    editBillingInfo(index, token, data, newkey){
      newkey = newkey.toLowerCase().replace(/\s/g, '')
      console.log("Hedef: ../cardinfos.json Eklenen kişi: " + index);
      this.$store.commit('updateBillingInfo', {obj: index, token: token, objdata: data, newkey: newkey})
    },
    deleteBillingData(index, token){
      console.log("Hedef: ../cardinfos.json Silinen kişi: " + index);
      this.$store.commit('updateBillingInfo', {obj: index, token: token})
    },
    accept(index, token){
      console.log("Kabul edildi.");
      this.deleteBillingData(index, token)
    },
    refuse(){
      console.log("Reddedildi.");
    }
  },
  mounted(){
    
  }
};
</script>
