<template>
  <div class="card h-100 mb-4">
    <div class="card-header pb-0 px-3">
      <div class="row">
        <div class="col-md-6">
          <h6 class="mb-0">Your Transaction's</h6>
        </div>
        <div class="col-md-6 d-flex justify-content-end align-items-center">
          <i class="far fa-calendar-alt me-2" aria-hidden="true"></i>
          <small>23 - 30 March 2020</small>
        </div>
      </div>
    </div>
    <div class="card-body pt-4 p-3">
      <h6 class="text-uppercase text-body text-xs font-weight-bolder mb-3">Newest</h6>
      <ul class="list-group">
        <li class="border-0 d-flex justify-content-center ps-0 mb-2 border-radius-lg" v-if="!transactionData">Loading...</li>
        <li
          v-else
          v-for="(transaction, index) in transactionData.newest"
          :key="index"
          class="list-group-item border-0 d-flex justify-content-between ps-0 mb-2 border-radius-lg"
        >
          <div class="d-flex align-items-center">
            <argon-button
              :color="setDangerMode(transaction.ispending, transaction.islost, false)"
              variant="outline"
              size="sm"
              class="btn-icon-only btn-rounded mb-0 me-3 d-flex align-items-center justify-content-center"
            >
              <i class="fas" :class="setDangerMode(transaction.ispending, transaction.islost, true)" aria-hidden="true"></i>
            </argon-button>
            <div class="d-flex flex-column">
              <h6 class="mb-1 text-dark text-sm">{{transaction.name}}</h6>
              <span class="text-xs">{{transaction.date}}</span>
            </div>
          </div>
          <div
            class="d-flex align-items-center text-gradient text-sm font-weight-bold"
            :class="setDangerMode(transaction.ispending, transaction.islost, null)"
          >{{transaction.price ? ((transaction.islost) ? '- $ ' : '+ $ ')+transaction.price : 'Pending'}}</div>
        </li>
      </ul>
      <h6 class="text-uppercase text-body text-xs font-weight-bolder my-3">Yesterday</h6>
      <ul class="list-group">
        <li class="border-0 d-flex justify-content-center ps-0 mb-2 border-radius-lg" v-if="!transactionData">Loading...</li>
        <li
          v-else
          v-for="(transaction, index) in transactionData.oldest"
          :key="index"
          class="list-group-item border-0 d-flex justify-content-between ps-0 mb-2 border-radius-lg"
        >
          <div class="d-flex align-items-center">
            <argon-button
              :color="setDangerMode(transaction.ispending, transaction.islost, false)"
              variant="outline"
              size="sm"
              class="btn-icon-only btn-rounded mb-0 me-3 d-flex align-items-center justify-content-center"
            >
              <i class="fas" :class="setDangerMode(transaction.ispending, transaction.islost, true)" aria-hidden="true"></i>
            </argon-button>
            <div class="d-flex flex-column">
              <h6 class="mb-1 text-dark text-sm">{{transaction.name}}</h6>
              <span class="text-xs">{{transaction.date}}</span>
            </div>
          </div>
          <div
            class="d-flex align-items-center text-gradient text-sm font-weight-bold"
            :class="setDangerMode(transaction.ispending, transaction.islost, null)"
          >{{transaction.price ? ((transaction.islost) ? '- $ ' : '+ $ ')+transaction.price : 'Pending'}}</div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import ArgonButton from "@/components/ArgonButton.vue";

export default {
  name: "transaction-card",
  components: {
    ArgonButton,
  },
  props:{
    transactionData: {
      default: null
    },
  },
  methods:{
    setDangerMode(ispending, islost, isreturningclass){
      if(isreturningclass == true || isreturningclass == false){
        switch(isreturningclass){
          case true:
            if(ispending){
              return 'fa-exclamation'
            }
            else if(islost){
              return 'fa-arrow-down'
            }
            return 'fa-arrow-up'
          case false:
            if(ispending){
              return 'dark'
            }
            else if(islost){
              return 'danger'
            }
            return 'success'
        }
      }
      else{
          if(ispending){
            return 'text-dark'
          }
          else if(islost){
            return 'text-danger'
          }
          return 'text-success'
      }
    },
  },
};
</script>
