<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-lg-8">
        <div class="row mt-4">
          <div class="col-xl-6 mb-xl-0 mb-4">
            <master-card :cardData="cardData" />
          </div>
          <div class="col-xl-6">
            <div class="row">
              <div class="col-md-6">
                <default-info-card
                  :classIcon="cardData ? cardData.master.salary.icon : ''"
                  :title="cardData ? cardData.master.salary.title : 'Loading...'"
                  :desc="cardData ? cardData.master.salary.description : 'Loading...'"
                  :price="cardData ? cardData.master.salary.price : 'Loading...'"
                />
              </div>
              <div class="col-md-6">
                <default-info-card
                  :classIcon="cardData ? cardData.master.freelancepayment.icon : ''"
                  :title="cardData ? cardData.master.freelancepayment.title : 'Loading...'"
                  :desc="cardData ? cardData.master.freelancepayment.description : 'Loading...'"
                  :price="cardData ? cardData.master.freelancepayment.price : 'Loading...'"
                />
              </div>
            </div>
          </div>
          <div class="col-md-12 mb-4">
            <payment-card :cardNoList="cardData ? getAllCardNumbers() : null" />
          </div>
        </div>
      </div>
      <div class="col-lg-4">
        <invoice-card class="mt-4" :invoiceData="cardData ? cardData.master.invoices : null" />
      </div>
    </div>
    <div class="row">
      <div class="col-md-7">
        <!-- <billing-card :billingdata="cardData ? cardData.master.billinginfo : null" :cachedbillingdata="cachedbillingData ? cachedbillingData : null" /> -->
        <billing-card  :billingdata="cachedbillingData ? cachedbillingData : null" />
      </div>
      <div class="col-md-5">
        <transaction-card :transactionData="cardData ? cardData.master.transactions : null" />
      </div>
    </div>
  </div>
</template>

<script>
import MasterCard from "@/examples/Cards/MasterCard.vue";
import DefaultInfoCard from "@/examples/Cards/DefaultInfoCard.vue";
import PaymentCard from "./components/PaymentCard.vue";
import InvoiceCard from "./components/InvoiceCard.vue";
import BillingCard from "./components/BillingCard.vue";
import TransactionCard from "./components/TransactionCard.vue";

import { mapGetters } from 'vuex';

export default {
  name: "Billing",
  components: {
    MasterCard,
    DefaultInfoCard,
    PaymentCard,
    InvoiceCard,
    BillingCard,
    TransactionCard,
  },

  computed:{
    ...mapGetters({
      cardData: 'getCardDatas',
      // TEST AMACLIDIR
      cachedbillingData: 'getCardBillingData'
    })
  },
  methods: {
    getAllCardNumbers(){
      let cachedCardNoList = {}
      for (var key in this.cardData) {
        if (Object.prototype.hasOwnProperty.call(this.cardData, key)) {
            cachedCardNoList[key] = {}
            cachedCardNoList[key].icon = this.cardData[key].icon
            cachedCardNoList[key].cardno = this.cardData[key].cardno
        }
      }
      return cachedCardNoList
    }
  }
};
</script>
