<th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2">Status</th>
<th class="text-uppercase text-secondary text-xxs font-weight-bolder text-center opacity-7 ps-2">Completion</th>
<th></th>
</tr>
</thead>
<tbody>
  <tr v-if="!projectsTable">
    <td>Loading...</td>
    <td>Loading...</td>
    <td class="align-middle">Loading...</td>
    <td class="align-middle text-center">Loading...</td>
  </tr>
  <tr v-for="project in projectsTable" :key="project.id">
    <td>
      <div class="d-flex px-2">
      <div>
      <img :src="require(`@/assets/img/small-logos/${project.logo}`)" class="avatar avatar-sm rounded-circle me-2" :alt="project.name">
      </div>
      <div class="my-auto">
      <h6 class="mb-0 text-sm">{{project.name}}</h6>
      </div>
      </div>
    </td>
    <td>
      <p class="text-sm font-weight-bold mb-0">{{project.budget}}</p>
    </td>
    <td>
      <span class="text-xs font-weight-bold">{{project.status}}</span>
    </td>
    <td class="align-middle text-center">
