          <div class="progress">
            <div class="progress-bar" :class="setProgressColor(project.progress)" role="progressbar" :aria-valuenow="project.progress" aria-valuemin="0" aria-valuemax="100" :style="`width: ${project.progress}%;`"></div>
          </div>
        </div>
      </div>
