<script>
import { mapGetters } from 'vuex'

export default {
    name: "projects-table",
    computed: {
      ...mapGetters({
        projectsTable: 'getProjectsTable'
      })
    },

    methods:{
      setProgressColor(progress){
        return (
          (progress <= 30) ? 'bg-gradient-danger' :
          (progress <= 99) ? 'bg-gradient-info' : 'bg-gradient-success'
        )
      }
    }
}
</script>
