<template>
  <div class="card">
    <div class="pb-0 card-header mb-0">
      <h6>{{chartData ? chartData.title : "Loading..."}}</h6>
      <p class="text-sm">
        <i class="fa fa-arrow-up text-success"></i>
        <span class="font-weight-bold">{{chartData ? chartData.percentage : "Loading..."}}</span>
        {{chartData ? chartData.year : "Loading..."}}
      </p>
    </div>
    <div class="p-3 card-body">
      <div class="chart">
        <canvas id="chart-line" class="chart-canvas" height="300"></canvas>
      </div>
    </div>
  </div>
</template>

<script>

import Chart from 'chart.js/auto';

import { mapGetters } from 'vuex'

export default {
  name: "gradient-line-chart",

  data(){
    return{
      ctx1: null,
      customChartColor: null,
      updatedChartDatabase: {
        label: "Loading...",
        data: [0,0,0,0,0,0,0,0,0],
      }
    }
  },

  computed: {
    ...mapGetters({
      chartData: 'getChartData'
    })
  },

  watch: {
    chartData(chartData){
      console.log("Grafiği yeniden çiz")
      
      let chartStatus = Chart.getChart("chart-line")
      if(chartStatus)
      {
        chartStatus.destroy()
      }
      new Chart(this.ctx1, {
      type: "line",
      data: {
        labels: ["Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
        datasets: [
          {
            label: chartData.label,
            tension: 0.4,
            borderWidth: 0,
            pointRadius: 0,
            borderColor: "#4BB543 ",
            backgroundColor: this.customChartColor,
            // eslint-disable-next-line no-dupe-keys
            borderWidth: 3,
            fill: true,
            data: chartData.data,
            maxBarThickness: 6,
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          legend: {
            display: false,
          },
        },
        interaction: {
          intersect: false,
          mode: "index",
        },
        scales: {
          y: {
            grid: {
              drawBorder: false,
              display: true,
              drawOnChartArea: true,
              drawTicks: false,
              borderDash: [5, 5],
            },
            ticks: {
              display: true,
              padding: 10,
              color: "#fbfbfb",
              font: {
                size: 11,
                family: "Open Sans",
                style: "normal",
                lineHeight: 2,
              },
            },
          },
          x: {
            grid: {
              drawBorder: false,
              display: false,
              drawOnChartArea: false,
              drawTicks: false,
              borderDash: [5, 5],
            },
            ticks: {
              display: true,
              color: "#ccc",
              padding: 20,
              font: {
                size: 11,
                family: "Open Sans",
                style: "normal",
                lineHeight: 2,
              },
            },
          },
        },
      },
      });
      
    }
  },

  mounted() {
    this.ctx1 = document.getElementById("chart-line").getContext("2d");

    this.customChartColor = this.ctx1.createLinearGradient(0, 230, 0, 50);

    this.customChartColor.addColorStop(1, "rgba(94, 114, 228, 0.2)");
    this.customChartColor.addColorStop(0.2, "rgba(94, 114, 228, 0.0)");
    this.customChartColor.addColorStop(0, "rgba(94, 114, 228, 0)");
    if(this.chartData)
    {
      this.updatedChartDatabase.label = this.chartData.label
      this.updatedChartDatabase.data = this.chartData.data
    }
    new Chart(this.ctx1, {
      type: "line",
      data: {
        labels: ["Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
        datasets: [
          {
            label: this.updatedChartDatabase.label,
            tension: 0.4,
            borderWidth: 0,
            pointRadius: 0,
            borderColor: "#4BB543 ",
            backgroundColor: this.customChartColor,
            // eslint-disable-next-line no-dupe-keys
            borderWidth: 3,
            fill: true,
            data: this.updatedChartDatabase.data,
            maxBarThickness: 6,
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          legend: {
            display: false,
          },
        },
        interaction: {
          intersect: false,
          mode: "index",
        },
        scales: {
          y: {
            grid: {
              drawBorder: false,
              display: true,
              drawOnChartArea: true,
              drawTicks: false,
              borderDash: [5, 5],
            },
            ticks: {
              display: true,
              padding: 10,
              color: "#fbfbfb",
              font: {
                size: 11,
                family: "Open Sans",
                style: "normal",
                lineHeight: 2,
              },
            },
          },
          x: {
            grid: {
              drawBorder: false,
              display: false,
              drawOnChartArea: false,
              drawTicks: false,
              borderDash: [5, 5],
            },
            ticks: {
              display: true,
              color: "#ccc",
              padding: 20,
              font: {
                size: 11,
                family: "Open Sans",
                style: "normal",
                lineHeight: 2,
              },
            },
          },
        },
      },
    });
  }


};
</script>
